@import url(https://use.fontawesome.com/releases/v5.3.1/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Joke {
  margin-bottom: 0.75em;
}

.Joke-votearea {
  display: inline-block;
  vertical-align: top;
}

.Joke-votearea > * {
  margin-right: 1em;

}
.Joke-text {
  margin-left: 1em;
  display: inline-block;
  width: 500px;
}
.loading {
  width: 4em;
  margin: 200px auto 0 auto;
  color: tomato;
}

.JokeList-getmore {
  margin: 1em 0;
  font-size: 150%;
  border-radius: 0.5em;
  background-color: tomato;
  color: white;
}
